body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  #animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

#components-layout-demo-top .logo {
  width: 120px;
  height: 31px;
  background: rgba(255,255,255,.2);
  margin: 16px 24px 16px 0;
  float: left;
}

.first-letter {
    font-size: 200%;
    color: red;
}

.Content-style {
    padding: 0 50px;
    min-height: 100vh;
}

.App-header {
  background-color: #aecce7;
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2.5vw;
  color: black;
  padding: 0px 0px 0px 0px;
}


.App-header-text {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2.5vw;
  font-weight: 500;
  color: mediumvioletred;
  padding-top: 0;
}
.App-header-text-2 {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2.15vw;
  color: darkblue;
  padding-top: 0;
}
.App-text {
  align-items: center;
  justify-content: center;
  font-size: 2vw;
  font-weight: 300;
  color: black;
}

.App-text-subheader {
  align-items: center;
  justify-content: center;
  font-size: 2vw;
  font-weight: 400;
  color: darkblue;
}

.App-text-small {
  align-items: left;
  justify-content: left;
  font-weight: 600;
  font-size: 1.7vw;
  color: black;
}

.schedule {   align-items: center;
  justify-content: center;
  font-size: 1.5vw;
  font-weight: 300;
  color: black;}

.picture {   align-items: center;
  justify-content: center;
  font-size: 1.25vw;
  font-weight: 300;
  color: black;}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.gutter-example .ant-row > div {
  background: transparent;
  border: 0;
  padding: 15px 15px;
}
.gutter-box {
  background: #456eff;
  padding: 10px 10px 10px 10px;
  width: 20vi;
  height: 6vw;
  color: white;
  font-size: calc(2vw - 0.25rem);
  text-align: center;
}


